@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0c0b0f;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position: relative;
  text-align: center;
}

.overlay{
  display: none;
}

.overlay.active {
  position: fixed;
  top: 0%;
  right: 0%;
  bottom: 0%;
  left: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  z-index: 12;
  opacity: 0.7;
}


.overlay > p{
  font-size: 4rem;
  font-weight: 400;
  color: #00fffc;
  /* animation: loadingAn 1s ease-in 0s infinite; */
}

@-webkit-keyframes loadingAn {
  0%{
    opacity: 0.2;
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0.2;
  }
}

@keyframes loadingAn {
  0%{
    opacity: 0.2;
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0.2;
  }
}

.navbar{
  height:80px;
  position: fixed;
  z-index: 10;
  background-color: #18171b;
  right:0%;
  left:0%;
  top:0%;
  display: flex;
  justify-content: center;
  box-shadow: 0 0 20px #000;
}


a{
  color: #019a9e;
  padding: 1rem 2rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 1.5rem;
  transition: all .7s;
}

a.active,
a:hover{
  color: #00fffc;
  background-color: #0c0b0f;
  text-shadow: 0 0 8px #019a9e;

}
.home{
  margin: 10rem 0;
  padding: 0;
  background-color: #0c0b0f;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #febe80;
}

h1{
  font-size: 3rem;
}

ul{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  line-height: 1.8;
  font-size: 1.2rem;
}

.userDetailCard > img{
  box-shadow: 10px 10px 40px #0a0a0b;
  margin: 0 0 1.5rem;
}

.userDetailCard > div{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.userDetailCard > div > h3 {
  color: #a26f3d;
  font-size: 1.3rem;
}


.userDetailCard > div > p{
  color: #ffe4ca;
}

.userlist{
  position: relative;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-areas: 'a a a b b b';
  padding: 0;
  width: 100%;
  max-width: 100%;
}

.cards{
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  right: 0%;
  left:0%;
  top: 0%;
  bottom: 0%;
  grid-area: a;
  background-color: #0c0b0f;
  margin:0;
  padding: 7rem 2.5rem 2rem;
  overflow-y: auto;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  transition: all .7s ease-in-out;
}
.cards::-webkit-scrollbar {
  /* Hide scrollbar for Chrome, Safari and Opera */
  display: none;
}

.cards.selected{
  right: 50%;
  margin: 0;
  padding: 7rem 0 2rem;
  background-color: #070609;
}

.cards > div{
  width: 100%;
  display: flex;
  justify-content: center;
  align-self: center;
}

.cards > div > a{
  width: 500px;
  margin: 1.5rem 0;
  padding: 0;
  transition: width .8s ease-in-out;
  text-shadow: none;
}


.cards.selected > div > a {
  width: 80%;
  margin: 1.5rem 0;
  padding: 0;
  background-color: #070609;

}

.card{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: #18171b;
  width: 100%;
  min-width: 340px;
  padding:1.5rem 0 1.5rem 1.2rem;
  margin: 0;
  border-radius: 20px;
  border-right: 8px solid #a26f3d;
  border-bottom: 2px solid #a26f3d;
  cursor: pointer;
  overflow:auto;
  box-shadow: 10px 10px 15px #000000;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  transition: all 0.7s;
}

.card > .userPhoto{
  align-self: flex-start;
}

.card > .userInfo{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 0 0 0 1rem;
}

p {
  color: black;
  margin: 0 0 1rem;
  color: #febe80;
  font-weight: 400;
  font-size: 1.2rem;
  transition: all .7s;
}

.card:hover, 
.card:focus,
.card:active{
  border-right: 8px solid #febe80;
  border-bottom: 2px solid #febe80;
  background-color: #1b1b1c;
  -webkit-transform: translateY(-4%);
          transform: translateY(-4%);
}

.card:hover > .userInfo > p
{
  color: #ffe4ca;
  text-shadow: none;
}

img{
  border-radius: 50%;
  box-shadow: 0 5px 15px #0c0b0f;
}

.userDetails{
  background-color: #0c0b0f;
  grid-area: b;
  padding: 8rem 2.5rem 2rem;
  margin:0;
}

.tableArea > p{
  margin: 10rem auto;
}

.tableArea > table{
  margin: 10rem auto;
  border: 2px solid #fff;
  border-collapse: collapse;
}

tr{
  border: 1px solid #fff;
}

th,
td{
  border: 1px solid #fff;
}

td > p{
  margin: .5rem 0;
}

th{
  color: #fff;
  font-size: 1.5rem;
  padding: .5rem 0;
}
.errorPage{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10rem 0;
  color: #febe80;
}

.errorCode{
  font-size: 10rem;
  font-weight: bolder;
}

.errorMessage{
  font-size: 2.5rem;
}
